import 'assets/css/gridMain.css';
import 'assets/css/deviceDetailHistory.css';
import 'react-datepicker/dist/react-datepicker.css';
import 'react-time-picker/dist/TimePicker.css';
import { Grid } from 'gridjs-react';
import 'gridjs/dist/theme/mermaid.min.css';
import { groupGridData, gridColumn } from 'app/data/deviceManagement/groups/gridData';
import { useNavigate } from 'react-router-dom';
import { useState } from 'react';
import DatePicker from 'react-datepicker';
import { TimePicker } from '@mui/x-date-pickers/TimePicker';
import TextField from '@mui/material/TextField';
import classnames from 'classnames';

function DeviceDetailHistory() {
  const navigate = useNavigate();
  const groupList = ['그룹1', '그룹2'];
  const [group, setGroup] = useState('');
  const versionList = ['미적용', '1.0.0.3', '1.0.0.2'];
  const [version, setVersion] = useState('');
  const [startDate, setStartDate] = useState(new Date());
  const [endDate, setEndDate] = useState(new Date());
  const [value1, setValue1] = useState(new Date());
  const [value2, setValue2] = useState(new Date());
  const [itemListBack, setItemListBack] = useState(['SoC', 'Volt', 'Current']);
  const [itemsList, setItemsList] = useState(itemListBack);
  const [items, setItems] = useState([]);

  const onChange = (e) => {
    const val = e.target.value;
    // const clone = itemListBack.slice();
    if (val !== '') {
      setItemsList(itemListBack.filter((el) => el.includes(val)));
    } else {
      setItemsList(itemListBack);
    }
  };

  return (
    <div className="section__grid__wrap content device__detail__history">
      <section className="device__detail__history">
        <div className="section__inner">
          <div className="search__wrap">
            <div className="left">
              <div className="selector__wrap">
                <dl>
                  <dt>검색필터</dt>
                  <dd>
                    <ul>
                      <li>
                        <select
                          className="custom__select"
                          onChange={(e) => setGroup(e.target.value)}
                        >
                          <option value="">디바이스를 선택</option>
                          {groupList.map((obj, idx) => (
                            <option key={obj} value={obj}>
                              {obj}
                            </option>
                          ))}
                        </select>
                      </li>
                      <li>
                        <select
                          className="custom__select"
                          onChange={(e) => setGroup(e.target.value)}
                        >
                          <option value="">디바이스 타입</option>
                          {groupList.map((obj, idx) => (
                            <option key={obj} value={obj}>
                              {obj}
                            </option>
                          ))}
                        </select>
                      </li>
                      <li>
                        <select
                          className="custom__select"
                          onChange={(e) => setGroup(e.target.value)}
                        >
                          <option value="">부품 타입</option>
                          {groupList.map((obj, idx) => (
                            <option key={obj} value={obj}>
                              {obj}
                            </option>
                          ))}
                        </select>
                      </li>
                      <li>
                        <select
                          className="custom__select"
                          onChange={(e) => setGroup(e.target.value)}
                        >
                          <option value="">부품 ID</option>
                          {groupList.map((obj, idx) => (
                            <option key={obj} value={obj}>
                              {obj}
                            </option>
                          ))}
                        </select>
                      </li>
                    </ul>
                  </dd>
                </dl>
                <dl>
                  <dt>수집일시</dt>
                  <dd>
                    <ul>
                      <li>
                        <DatePicker selected={startDate} onChange={(date) => setStartDate(date)} />
                      </li>
                      <li>
                        <TimePicker
                          value={value1}
                          onChange={setValue1}
                          renderInput={(params) => <TextField {...params} />}
                        />
                      </li>
                      <li>
                        <DatePicker selected={endDate} onChange={(date) => setEndDate(date)} />
                      </li>
                      <li>
                        <TimePicker
                          value={value2}
                          onChange={setValue2}
                          renderInput={(params) => <TextField {...params} />}
                        />
                      </li>
                    </ul>
                  </dd>
                </dl>
              </div>
            </div>
            <div className="right">
              <div>
                <dl>
                  <dt>
                    <input
                      onChange={onChange}
                      type="text"
                      className="custom__input"
                      placeholder="Check Items..."
                    />
                  </dt>
                  <dd className="vertical__scroll">
                    <ul>
                      {itemsList.map((obj, idx) => (
                        <li
                          key={obj}
                          value={obj}
                          className={classnames('asd', [
                            {
                              active: items.includes(obj),
                            },
                          ])}
                          onClick={() => {
                            items.includes(obj)
                              ? setItems(items.filter((el) => el !== obj))
                              : setItems(items.concat(obj));
                            console.log(items);
                          }}
                        >
                          {obj}
                        </li>
                      ))}
                    </ul>
                  </dd>
                </dl>
              </div>
              <button className="custom__btn">
                <svg fill="none" viewBox="0 0 24 24" stroke="currentColor">
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    strokeWidth="2"
                    d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z"
                  />
                </svg>
                <span className="f__medium">검색</span>
              </button>
            </div>
          </div>
          <div className="grid__wrap">
            <Grid
              sort="true"
              className="grid"
              data={groupGridData}
              columns={gridColumn}
              pagination={{
                enabled: true,
                limit: 10,
              }}
            />
          </div>
        </div>
      </section>
    </div>
  );
}

export default DeviceDetailHistory;
